.wp-admin .acf-tooltip li {
  position: relative;
}
.wp-admin .acf-tooltip li a {
  padding-left: 30px;
}
.wp-admin .acf-tooltip li a:after {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 3px;
  left: 5px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 0.3s ease;
}
.wp-admin .acf-tooltip li a:hover:after {
  width: 300px;
  height: 300px;
  left: -300px;
  top: 0;
  background-position: center top;
}
.wp-admin .acf-tooltip li a[data-layout=hero_banner]:after {
  background-image: url("../../assets/images/cms-icons/hero-banner.webp");
}
.wp-admin .acf-tooltip li a[data-layout=about_us]:after {
  background-image: url("../../assets/images/cms-icons/description.webp");
}
